@import url(https://fonts.googleapis.com/css?family=Raleway);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  font-family: "Raleway", sans-serif;
}
.container {
  max-width: 750px;
  width: 100%;
  border: 1px solid rgba(0, 0, 0, 0.2);
  box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  padding: 10px;
  box-sizing: border-box;
  margin: 5px auto auto;
}
.title {
  position: relative;
  background-color: rgba(0, 0, 0, 1);
  color: rgb(255, 255, 255);
  padding: 20px;
  font-size: 1.8em;
  border-radius: 5px;
}
.question {
  font-size: 16px;
  font-weight: bold;
}
.questionBox {
  text-align: left;
  position: relative;
  border: 1px solid rgba(0, 0, 0, 0.2);
  margin-top: 5px;
  padding: 10px;
  box-sizing: border-box;
  background-color: rgb(250, 250, 250);
}
.answerBtn {
  position: relative;
  padding: 10px;
  background-color: rgb(250, 151, 37);
  background-color: rgb(232 102 102);
  display: inline-block;
  margin: 10px 5px;
  outline: none;
  border: none;
  font-size: 15px;
  color: rgb(255, 255, 255);
  font-weight: bold;
  border-radius: 4px;
  cursor: pointer;
}
.score-board {
  position: relative;
  height: 550px;
}
.score {
  position: absolute;
  font-size: 3em;
  top: 170px;
  text-align: center;
}
.playBtn {
  border: none;
  position: absolute;
  font-size: 1.4em;
  padding: 10px;
  top: 300px;
  background-color: rgb(46, 182, 46);
  color: rgb(255, 255, 255);
  width: 150px;
  left: 0px;
  right: 0px;
  margin: auto;
  outline: none;
  cursor: pointer;
}
@media only screen and (max-width: 767px) {
  .container {
    width: 95%;
 }
 .question{
  font-size: 15px;
 }
 .answerBtn{
   font-size: 14px;
 }
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

